import { Grid, Box, Card, useTheme, Button, Typography } from "@mui/material";
import { useContext, useState } from "react";
import { CoinContext } from '../contexts/CoinContext'
import HeaderCard from "./header/HeaderCard";
import Graph  from './Graph'

const responsive = {
    width: {xs: '100%', sm: '90%', md:'60%'},
    mx: "auto"
}

export default function Body() {
  const { coinPrices, coinInfo } = useContext(CoinContext)
  const [days, setDays] = useState(30)
  const theme = useTheme()
  const handleClick = (value) => {
    setDays(parseInt(value))
  }

  console.log(coinInfo)

    return (
        <Box sx={{...responsive, 
        flexGrow: 1, 
        mt: "5vh", 
        boxShadow: theme.shadows[1],
        }}>
          <Grid container sx={{padding: "6vh"}}>
            <Grid 
            item xs={3} 
            sx={{flexDirection: "column"}}
            >
              <HeaderCard title="ATH" value={coinInfo['ath']}/>
              <HeaderCard title="Market Cap" value={coinInfo['market_cap']} />
              <HeaderCard title="Circulating Supply" value={coinInfo['circulating_supply']}/>
            </Grid>
            <Grid item xs={9}>
              <Card sx={{height: "36vh"}}>
                <Graph data={coinPrices} days={days} coinName={coinInfo.name}/>
              </Card>
            </Grid>
            <Grid 
            item xs={9} 
            sx={{display: "flex",alignItems: 'center',justifyContent: 'end', paddingTop: "2vh"}}
            >
              <Button id="7" onClick={e => handleClick(e.target.id)}>7d</Button>
              <Button id="14" onClick={e => handleClick(e.target.id)}>14d</Button>
              <Button id="30" onClick={e => handleClick(e.target.id)}>30d</Button>
            </Grid>
            <Grid
            item xs={4}
            sx={{display: 'flex', alignItems: 'center'}}
            >
              <Box
              component="img"
              src={coinInfo.image}
              sx={{height: "10vh"}}
              >  
              </Box>
              <Typography 
                variant="h4"
                sx={{ml:'2vh'}}
                >
                  {coinInfo.name}
              </Typography>
            </Grid>
          </Grid>
        </Box>

    );
}