import { alpha, styled, InputBase, Autocomplete } from '@mui/material';
import SearchIcon from '@mui/icons-material/Search';
import { useEffect, useState, useContext } from 'react';
import { FetchCoins, FetchPrices } from './api/api.js';
import { CoinContext } from '../contexts/CoinContext'

export default function SearchField() {
  const [coins, setCoins] = useState([])
  const { dispatch, dispatchInfo } = useContext(CoinContext)

  const handleSelect = (coin) => {
      FetchPrices(coin.id)
      .then(data => {
        dispatch({type: 'ADD_PRICES', data: data})
        dispatchInfo({type: 'ADD_INFO', data: coin})
      })
  }

  const Search = styled('div')(({ theme }) => ({
      position: 'relative',
      borderRadius: theme.shape.borderRadius,
      backgroundColor: alpha(theme.palette.common.white, 0.15),
      '&:hover': {
        backgroundColor: alpha(theme.palette.common.white, 0.25),
      },
      marginLeft: 0,
      width: '100%',
      [theme.breakpoints.up('sm')]: {
        marginLeft: theme.spacing(1),
        width: 'auto',
      },
    }));
  
    const SearchIconWrapper = styled('div')(({ theme }) => ({
      padding: theme.spacing(0, 2),
      height: '100%',
      position: 'absolute',
      pointerEvents: 'none',
      display: 'flex',
      alignItems: 'center',
      justifyContent: 'center',
    }));
    
    // Materialui Input
    const StyledInputBase = styled(InputBase)(({ theme }) => ({
      color: 'inherit',
      '& .MuiInputBase-input': {
        padding: theme.spacing(1, 1, 1, 0),
        // vertical padding + font size from searchIcon
        paddingLeft: `calc(1em + ${theme.spacing(4)})`,
        transition: theme.transitions.create('width'),
        width: '100%',
        [theme.breakpoints.up('sm')]: {
          width: '12ch',
          '&:focus': {
            width: '20ch',
          },
        },
      },
    }));

    useEffect(() => {
      FetchCoins()
      .then(data => setCoins(data))
    }, [])

    return (
      <Search>
          <SearchIconWrapper>
              <SearchIcon />
          </SearchIconWrapper>
          <Autocomplete
          id="combo-box-demo"
          options={coins}
          getOptionLabel={option => option.id}
          onChange={(e, value) => handleSelect(value)}
          style={{ width: 300 }}
          renderInput={params => {
            const {InputLabelProps,InputProps,...rest} = params;
            return <StyledInputBase
            placeholder="Search…"
            {...params.InputProps} 
            {...rest}
            />}}
          />
      </Search>
    )
}