import { LineChart, Line, XAxis, YAxis, Tooltip, ResponsiveContainer, Legend } from 'recharts';
import { useTheme } from '@mui/material/styles';

export default function Graph(props) {
    const theme = useTheme()
    const monthNames = ["Jan", "Feb", "Mar", "Apr", "May", "Jun", "Jul", "Aug", "Sep", "Oct", "Novr", "Dec"];
    let formattedData = []
    let minPrice = 0
    let maxPrice = 0

    if (Object.keys(props.data).length) {
        props.data.prices.slice(-props.days, -1).forEach((item, idx) => {
            let date = new Date(item[0])
            formattedData.push({'date': `${date.getDate()}-${monthNames[date.getMonth()]}`, 'price': item[1]})
            maxPrice = Math.max.apply(Math, formattedData.map(function(o) { return o.price; }))
            minPrice = Math.min.apply(Math, formattedData.map(function(o) { return o.price; }))
        })
    }

    return (
        <ResponsiveContainer 
        width="100%" 
        height="100%"
        >
        {props.data && 
            <LineChart
                width={500}
                height={300}
                data={formattedData}
                margin={{ top: 15, right: 30, left: 20, bottom: 5 }}
            >
                <XAxis 
                dataKey='date' 
                tickCount={5} 
                interval={4}
                tickLine={false}
                width={3}
                />
                <YAxis 
                domain={[ Math.round(minPrice * 0.9 *100) /100, Math.round(maxPrice * 1.05 * 100) /100]}
                tickLine={false}
                scale='sqrt'
                >
                </YAxis>
                <Tooltip />
                <Legend verticalAlign="top" height={36} payload={[{value: `Price ${props.coinName} (USD)`, type: 'line', id: 'ID01'}]}/>
                <Line 
                type="monotone" 
                dataKey="price"
                stroke={theme.palette.primary.main} 
                activeDot={{ r: 9 }} 
                dot={false}
                strokeWidth={3}
                />
            </LineChart>
            }
        </ResponsiveContainer> 
    );
}